import React from 'react';
import Header from './components/header.js';
import Content from './components/content.js';
import Footer from './components/footer.js';
import Generic from './components/genericSolutions.js';
import Industry from './components/industrySpecific.js';
import {Route, NavLink, HashRouter} from 'react-router-dom';
import './App.css';

function App() {
  return (
    <HashRouter>
      <div>
        <Header/>
        <Route exact path="/" component={Content}/>
        <Route path="/genericSolutions" component={Generic}/>
        <Route path="/industrySpecific" component={Industry}/>
        <Footer/>
      </div>
    </HashRouter>
  );
}

export default App;
